.fixed-height {
  height: 14rem;
  margin-top: 0;
}

.card-image-topper {
  margin-left: auto;
  margin-right: auto;
  padding-left: 8px;
  padding-right: 8px;
  object-fit: cover;
  object-position: center;
  width: inherit;
  height: 10rem;
}

.card-details-bordering {
  border-radius: 0 0 0.25rem 0.25rem !important;
  margin-top: 0 !important;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  margin-bottom: 0.5rem;
}